import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";

export const getHeaders = (): ITableHeader[] => [
  { name: "id" },
  { name: "" },
  { name: "" },
  { name: "" },
];

export const getRows = (items: any[]): ITableRow[] => {
  return items.map(({ id, name, address, coin }: any) => ({
    columns: [
      {
        content: id,
      },
      {
        content: name,
      },
      {
        content: address || "3FZbgi29cpjq2GjdwV8eyHuJJnkLtktZc5",
      },
      {
        coin: coin || "BTC",
      },
    ],
  }));
};
