import { getDate } from "utils/getDate";
import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";
import { IOrder } from "store/ducks/owner/state";

export const getHeaders = (): ITableHeader[] => [
  { name: "" },
  { name: "id" },
  { name: "" },
  { name: "" },
  { name: " ()" },
  { name: " " },
  { name: " " },
];

export const getRows = (items: any[]): ITableRow[] => {
  return items.map(({ id, status, shop, createDate, amount, payerEmail, payerCardNumber }: IOrder) => ({
    columns: [
      {
        status,
      },
      {
        content: id,
      },
      {
        content: shop.user.name,
      },
      {
        content: getDate(createDate),
      },
      {
        content: amount,
      },
      {
        content: payerEmail || "",
      },
      {
        content: payerCardNumber || "",
      },
    ],
  }));
};
