import { SagaIterator } from "redux-saga";
import { all, call, put, takeEvery } from "redux-saga/effects";
import { cookies } from "plugins/cookies";

import { history } from "utils/history";
import { errorHandler } from "utils/errorHandler";

import { API_MANAGE } from "api/manage";
import { setAuthStatus } from "../auth/actionCreators";
import { Status } from "../auth/state";

import {
  ManageActionsTypes,
  IChangeNameWithEmail,
  IChangePassword,
  setUser,
  IAddShop,
  setShops,
  IEditShop,
  IDeleteShop,
  IRefreshKey,
  setKey,
  IFetchShops,
} from "./actionCreators";

export function* changeNameWithEmail({ payload: values }: IChangeNameWithEmail): any {
  try {
    yield call(API_MANAGE.changeNameWithEmail, values);
  } catch (e) {
    errorHandler(e);
  }
}

export function* changePassword({ payload: values }: IChangePassword): any {
  try {
    yield call(API_MANAGE.changePassword, values);
  } catch (e) {
    if (e.response.data) {
      const errorCode = e.response.data.error.code;

      if (errorCode === 4013) {
        alert("    ,     ");
      }
    } else {
      errorHandler(e);
    }
  }
}

export function* fetchUser(): any {
  try {
    const data = yield call(API_MANAGE.fetchUser);

    if (data) {
      yield put(setUser(data));
    } else {
      yield put(setAuthStatus(Status.NEVER));

      cookies.set("token", "", { path: "/", expires: new Date(Date.now() - 1000) });
      history.push("/auth");
      alert(" ");
    }
  } catch (e) {
    yield put(setAuthStatus(Status.NEVER));

    cookies.set("token", "", { path: "/", expires: new Date(Date.now() - 1000) });
    history.push("/auth");
    alert(" ");
    console.log(e);
  }
}

export function* fetchShops({ payload: values }: IFetchShops): any {
  try {
    const data = yield call(API_MANAGE.fetchShops, values);
    yield put(setShops(data));
  } catch (e) {
    errorHandler(e);
  }
}
export function* addShop({ payload: values }: IAddShop): any {
  try {
    const data = yield all([yield call(API_MANAGE.addShop, values), yield call(API_MANAGE.fetchShops, { page: 0 })]);

    yield put(setShops(data[1]));
  } catch (e) {
    errorHandler(e);
  }
}
export function* editShop({ payload: values }: IEditShop): any {
  try {
    const data = yield all([yield call(API_MANAGE.editShop, values), yield call(API_MANAGE.fetchShops, { page: 0 })]);

    yield put(setShops(data[1]));
  } catch (e) {
    errorHandler(e);
  }
}
export function* deleteShop({ payload: values }: IDeleteShop): any {
  try {
    yield call(API_MANAGE.deleteShop, values);
  } catch (e) {
    errorHandler(e);
  }
}

export function* refreshKey({ payload: values }: IRefreshKey): any {
  try {
    const data = yield call(API_MANAGE.refreshKey, values);
    const key = Object.keys(data)[1];

    yield put(setKey({ id: values.id, [key]: data[key] }));
  } catch (e) {
    errorHandler(e);
  }
}

export function* manageSaga(): SagaIterator {
  yield takeEvery(ManageActionsTypes.CHANGE_NAME_EMAIL, changeNameWithEmail);
  yield takeEvery(ManageActionsTypes.CHANGE_PASSWORD, changePassword);

  yield takeEvery(ManageActionsTypes.FETCH_USER, fetchUser);

  yield takeEvery(ManageActionsTypes.FETCH_SHOPS, fetchShops);
  yield takeEvery(ManageActionsTypes.ADD_SHOP, addShop);
  yield takeEvery(ManageActionsTypes.EDIT_SHOP, editShop);
  yield takeEvery(ManageActionsTypes.DELETE_SHOP, deleteShop);

  yield takeEvery(ManageActionsTypes.KEY_REFRESH, refreshKey);
}
