import { FC } from "react";
import clsx from "clsx";

import { makeStyles, Tooltip } from "@material-ui/core";
import { green, red, yellow } from "@material-ui/core/colors";

import { IShop } from "store/ducks/manage/state";

interface Props {
  data: IShop["verifyStatus"];
}

const useStyles = makeStyles(theme => ({
  status: {
    marginLeft: theme.spacing(1),
    width: theme.spacing(1),
    height: theme.spacing(1),
    backgroundColor: theme.palette.text.disabled,
    borderRadius: "100%",
    "&:hover": {
      transform: "scale(1.25)",
    },
  },
  success: {
    backgroundColor: green.A400,
  },
  processing: {
    backgroundColor: yellow[600],
  },
  error: {
    backgroundColor: red.A400,
  },
}));

const getStatus = (status: Props["data"]): string => {
  switch (status) {
    case 1:
      return " ";
    case 2:
      return "";
    case 3:
      return "";
    default:
      return " ";
  }
};

export const Status: FC<Props> = ({ data }) => {
  const s = useStyles();

  return (
    <Tooltip title={getStatus(data)} placement="right">
      <div
        className={clsx(s.status, {
          [s.processing]: data === 1,
          [s.success]: data === 2,
          [s.error]: data === 3,
        })}
      />
    </Tooltip>
  );
};
