import { FC, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Location, History } from "history";
import { useForm } from "react-hook-form";

import { Button, Grid, Menu, MenuItem, List, ListItem, Typography } from "@material-ui/core";
import { Delete, Equalizer, PowerSettingsNew } from "@material-ui/icons";
import Input from "components/UI/Fields/Input";
import { SitesTable } from "components/Pages/Home/SitesTable";

import { getPrice } from "utils/getPrice";

import { RootState } from "store/rootReducer";
import { IPartner } from "store/ducks/owner/state";
import { deletePartner, editPartner } from "store/ducks/owner/actionCreators";
import { selectPartner } from "store/ducks/owner/selectors";

interface RouteProps<P> {
  match: { params: P };
  location: Location;
  history: History;
}

interface Props {
  id: IPartner["id"];
}

interface IDefaultValues {
  email: string;
  password: string;
  description: string;
  commission: number;
}

const PartnerPage: FC<RouteProps<Props>> = ({ match }) => {
  const { id } = match.params;
  const dispatch = useDispatch();
  const partner = useSelector((state: RootState) => selectPartner(state, Number(id)) as any);

  const defaultValues: IDefaultValues = {
    email: partner?.email,
    password: "",
    description: partner?.description,
    commission: partner?.commission,
  };
  const { control, watch, reset } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});
  const email = watch("email");
  const password = watch("password");
  const description = watch("description");
  const commission = watch("commission");

  const handleSubmit = () => {
    setErrors({});
    if (!email || !/\S+@\S+\.\S+/.test(email)) {
      setErrors((prev: any) => ({
        ...prev,
        email: "  ",
      }));
      return;
    }

    if (description.length > 128) {
      setErrors((prev: any) => ({
        ...prev,
        description: "    128 ",
      }));
      return;
    }

    if (Number(commission) > 100) {
      setErrors((prev: any) => ({
        ...prev,
        commission: " 5%,  100%",
      }));
      return;
    }

    const action = dispatch(editPartner({ id, email, password, description, commission }));

    reset({
      email: action.payload.email,
      password: "",
      description: action.payload.description,
      commission: action.payload.commission,
    });
  };

  const [stats, setStats] = useState<null | HTMLElement>(null);
  const [period, setPeriod] = useState("");

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => setStats(event.currentTarget);
  const handlePickStats = (e: any) => {
    e.currentTarget.innerText && setPeriod(e.currentTarget.innerText);
    setStats(null);
  };

  const handleDelete = () => {
    if (window.confirm(`     "${partner?.name}"?`)) {
      dispatch(deletePartner(id));
    }
  };

  return (
    <Grid>
      <Typography variant="h4" gutterBottom>
        {partner?.name} (id: {partner?.id})
      </Typography>

      <Grid container>
        <Grid item xs={12} md={6} lg={8}>
          <Button onClick={handleClick} startIcon={<Equalizer />}>
              {period}
          </Button>
          <List dense>
            <ListItem> : 3</ListItem>
            <ListItem> : 1</ListItem>
            <ListItem> : {getPrice(100)}</ListItem>
          </List>
          <Menu anchorEl={stats} keepMounted open={Boolean(stats)} onClose={handlePickStats}>
            <MenuItem onClick={handlePickStats}></MenuItem>
            <MenuItem onClick={handlePickStats}></MenuItem>
            <MenuItem onClick={handlePickStats}></MenuItem>
          </Menu>
        </Grid>
        <Grid item xs={12} md={6} lg={4}>
          <form id="editPartner" style={{ marginBottom: 28 }}>
            <Grid container>
              <Grid item>
                <Input
                  control={control}
                  name="email"
                  label="Email"
                  type="email"
                  defaultValue={defaultValues.email}
                  error={!!errors.email}
                  helperText={errors?.email}
                  required
                  autoFocus
                />
                <Input
                  control={control}
                  name="webhookURL"
                  label=" "
                  type="password"
                  defaultValue={defaultValues.password}
                  error={!!errors.password}
                  helperText={errors?.password}
                />
              </Grid>
              <Grid container spacing={1}>
                <Grid item xs={12} sm={8}>
                  <Input
                    control={control}
                    name="description"
                    label=""
                    type="text"
                    defaultValue={defaultValues.description}
                    error={!!errors.description}
                    helperText={errors?.description}
                  />
                </Grid>
                <Grid item xs={12} sm={4}>
                  <Input
                    control={control}
                    name="commission"
                    label=", %"
                    type="number"
                    min={5}
                    max={100}
                    defaultValue={defaultValues.commission}
                    error={!!errors.commission}
                    helperText={errors?.commission}
                    required
                  />
                </Grid>
              </Grid>

              <Button color="primary" onClick={handleSubmit} fullWidth>
                
              </Button>
            </Grid>

            <Grid container spacing={1}>
              <Grid item xs={12} sm={6}>
                <Button
                  variant="outlined"
                  color="secondary"
                  startIcon={<Delete />}
                  onClick={handleDelete}
                  fullWidth
                  style={{ marginTop: "1rem" }}>
                   
                </Button>
              </Grid>
              <Grid item xs={12} sm={6}>
                <Button
                  variant="outlined"
                  color="primary"
                  startIcon={<PowerSettingsNew />}
                  // onClick={handleClose}
                  fullWidth
                  style={{ marginTop: "1rem" }}>
                  
                </Button>
              </Grid>
            </Grid>
          </form>
        </Grid>
      </Grid>

      <SitesTable partnerId={partner?.id} />
    </Grid>
  );
};

export default PartnerPage;
