import { FC, ReactElement, useState } from "react";
import { useHistory } from "react-router-dom";
// import { useDispatch, useSelector } from "react-redux";

import { Button, Paper, Menu, MenuItem } from "@material-ui/core";
import { Add as AddIcon } from "@material-ui/icons";
import Table from "components/UI/TableWrapper";
// import { NewDialog } from "./NewDialog";

import { getHeaders, getRows } from "./utilsTable";
// import { selectPartners } from "store/ducks/owner/selectors";
// import { fetchPartners } from "store/ducks/owner/actionCreators";

export const TerminalsTable: FC = (): ReactElement => {
  // const dispatch = useDispatch();
  const history = useHistory();
  // const terminals = useSelector(selectPartners);
  const terminals = [
    {
      id: 1,
      status: 2,
      name: " ",
    },
  ];

  const headers = getHeaders();
  const rows = getRows(terminals);

  const [stats, setStats] = useState<null | HTMLElement>(null);
  const [newTerminal, setNewTerminal] = useState<boolean>(false);

  const onRowClick = (e: any, id: number) => history.push(`/terminals/${id}`);

  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => setStats(event.currentTarget);
  const handleNewTerminal = (e: any) => {
    e.currentTarget.innerText && setNewTerminal(e.currentTarget.innerText);
    console.log(newTerminal);
    setStats(null);
  };

  // useEffect(() => {
  //   dispatch(fetchPartners());
  // }, [dispatch]);

  return (
    <Paper>
      <Table
        headers={headers}
        rows={rows}
        title=" "
        button={
          <>
            <Button variant="outlined" color="primary" startIcon={<AddIcon />} onClick={handleClick}>
              
            </Button>
            <Menu anchorEl={stats} keepMounted open={Boolean(stats)} onClose={handleNewTerminal}>
              {/* TODO: .map() */}
              <MenuItem onClick={handleNewTerminal}> </MenuItem>
              <MenuItem onClick={handleNewTerminal}> </MenuItem>
            </Menu>
          </>
        }
        onRowClick={onRowClick}
      />

      {/* <NewDialog open={openNew} handleClose={handleNewClose} /> */}
    </Paper>
  );
};
