import { FC, ChangeEvent, useState } from "react";
// import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";

import { Button, TextField, MenuItem } from "@material-ui/core";
import { Delete } from "@material-ui/icons";
import Dialog from "components/UI/Dialog";
import Input from "components/UI/Fields/Input";

// import { getPrice } from "utils/getPrice";

// import { RootState } from "store/rootReducer";
// import { deletePartner } from "store/ducks/owner/actionCreators";
// import { selectPartner } from "store/ducks/owner/selectors";

interface Props {
  id: number | null;
  handleClose: () => void;
}

interface IDefaultValues {
  name: string;
  address: string;
}

const currencies = [
  {
    value: "BTC",
    label: "",
  },
  {
    value: "USDT",
    label: "",
  },
  {
    value: "ETH",
    label: "",
  },
];

export const EditDialog: FC<Props> = ({ id, handleClose }) => {
  // const dispatch = useDispatch();
  // const wallet = useSelector((state: RootState) => id && (selectPartner(state, id) as any));
  const wallet = {
    id: 1,
    name: "",
    address: "3FZbgi29cpjq2GjdwV8eyHuJJnkLtktZc5",
    coin: "/img/coins/btn.svg",
  };

  const [currency, setCurrency] = useState("BTC");
  const handleChange = (e: ChangeEvent<HTMLInputElement>) => setCurrency(e.target.value);

  const defaultValues: IDefaultValues = {
    name: wallet.name,
    address: wallet.address,
  };
  const { control, handleSubmit, watch } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});

  const name = watch("name");
  // const address = watch("address");

  const onSubmit = () => {
    setErrors({});

    if (name.length < 3) {
      setErrors((prev: any) => ({
        ...prev,
        name: "     3  ",
      }));
    }

    setErrors({});
    handleClose();
  };

  const handleDelete = () => {
    if (id && window.confirm(`     "${wallet.name}"?`)) {
      handleClose();
      // dispatch(deletePartner(id));
    }
  };

  return (
    <Dialog open={Boolean(id)} handleClose={handleClose} title={`${wallet.name} (id: ${wallet.id})`} form="editWallet">
      <form id="editWallet" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="name"
          label=""
          type="text"
          defaultValue={defaultValues.name}
          error={!!errors.name}
          helperText={errors?.name}
        />
        <Input
          control={control}
          name="address"
          label=""
          type="number"
          defaultValue={defaultValues.address}
          error={!!errors.address}
          helperText={errors?.address}
        />
        <TextField
          label=""
          value={currency}
          onChange={handleChange}
          variant="outlined"
          style={{ marginTop: 16 }}
          select
          required
          fullWidth>
          {currencies.map(option => (
            <MenuItem key={option.value} value={option.value}>
              {option.label} ({option.value})
            </MenuItem>
          ))}
        </TextField>
        <Button
          variant="outlined"
          color="secondary"
          startIcon={<Delete />}
          onClick={handleDelete}
          style={{ marginTop: "1rem" }}
          fullWidth>
           
        </Button>
      </form>
    </Dialog>
  );
};
