import { getDate } from "utils/getDate";
import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";
// import { IPartner } from "store/ducks/owner/state";

export const getHeadersAndRows = (type?: string) => {
  const items =
    type === "requests"
      ? [
          {
            id: 1,
            amount: 1000,
            createDate: Date.now() / 1000,
            ip: " ",
          },
        ]
      : [
          {
            id: 1,
            name: "test",
            amount: 1000,
            createDate: Date.now() / 1000,
            ip: " ",
          },
        ];

  const headers: ITableHeader[] =
    type === "requests"
      ? [{ name: "id" }, { name: " ()" }, { name: " " }, { name: "" }]
      : [
          { name: "id" },
          { name: "" },
          { name: " ()" },
          { name: " " },
          { name: "" },
        ];

  const rows: ITableRow[] = items.map(({ id, name, amount, createDate, ip }: any) =>
    type === "requests"
      ? {
          columns: [
            {
              content: id,
            },
            {
              content: amount,
            },
            {
              content: getDate(createDate),
            },
            {
              content: ip,
            },
          ],
        }
      : {
          columns: [
            {
              content: id,
            },
            {
              content: name,
            },
            {
              content: amount,
            },
            {
              content: getDate(createDate),
            },
            {
              content: ip,
            },
          ],
        },
  );

  return { headers, rows };
};
