import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";
import { ITerminal } from "store/ducks/owner/state";

export const getHeaders = (): ITableHeader[] => [
  { name: "" },
  { name: "id" },
  { name: "" },
  { name: "" },
  { name: " " },
  { name: " " },
];

export const getRows = (items: ITerminal[] | any): ITableRow[] => {
  return items.map(({ id, status, name, visitors, payments, clients }: ITerminal | any) => ({
    columns: [
      {
        status,
      },
      {
        content: id,
      },
      {
        content: name,
      },
      {
        content: visitors || "39",
      },
      {
        content: payments || "148",
      },
      {
        content: clients || "24",
      },
    ],
  }));
};
