import { FC, ReactElement, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { Paper, Button } from "@material-ui/core";
import { Add as AddIcon } from "@material-ui/icons";
import Table from "components/UI/TableWrapper";
import { NewDialog } from "./NewDialog";
import { EditDialog } from "./EditDialog";

import { getHeaders, getRows } from "./utilsTable";

import { fetchShops } from "store/ducks/manage/actionCreators";
import { selectShops, selectUser } from "store/ducks/manage/selectors";
import { IPartner } from "store/ducks/owner/state";

interface Props {
  partnerId?: IPartner["id"];
}

export const SitesTable: FC<Props> = ({ partnerId }): ReactElement => {
  const dispatch = useDispatch();
  const shops = useSelector(selectShops);
  const user = useSelector(selectUser);

  const headers = getHeaders();
  const rows = getRows(shops || []);

  const [page, setPage] = useState(0);
  const [openNew, setOpenNew] = useState<boolean>(false);
  const [editID, setEditID] = useState<number | null>(null);

  const handleNewOpen = () => setOpenNew(true);
  const handleNewClose = () => setOpenNew(false);

  const onRowClick = (e: any, id: number) => setEditID(id);
  const handleEditClose = () => setEditID(null);

  useEffect(() => {
    if (partnerId) {
      dispatch(fetchShops({ partnerId, page }));
    } else {
      if (shops && !shops.length) {
        dispatch(fetchShops({ page }));
      }
    }
  }, [dispatch, partnerId, page, shops]);

  return (
    <Paper>
      <Table
        headers={headers}
        rows={rows}
        title=" "
        button={
          user.type === 1 ? (
            <Button variant="outlined" color="primary" startIcon={<AddIcon />} onClick={handleNewOpen}>
              
            </Button>
          ) : undefined
        }
        onRowClick={onRowClick}
        currentPage={page}
        setCurrentPage={setPage}
      />

      {user.type === 1 && <NewDialog open={openNew} handleClose={handleNewClose} />}
      <EditDialog id={editID} handleClose={handleEditClose} />
    </Paper>
  );
};
