import { Action } from "redux";
import { ILogin, IAuth } from "./state";

export enum AuthActionsTypes {
  LOGIN = "auth/LOGIN",
  LOGOUT = "auth/LOGOUT",
  SET_STATUS = "auth/SET_STATUS",
  SET_TOKEN = "auth/SET_TOKEN",
}

// ACTIONS TYPES
export interface ILoginAction extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.LOGIN;
  payload: ILogin;
}
export interface ILogoutAction extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.LOGOUT;
}

export interface IAuthStatus extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.SET_STATUS;
  payload: IAuth["status"];
}
interface IAuthToken extends Action<AuthActionsTypes> {
  type: AuthActionsTypes.SET_TOKEN;
  payload: string;
}

// ACTIONS
export const login = (payload: ILoginAction["payload"]): ILoginAction => ({
  type: AuthActionsTypes.LOGIN,
  payload,
});
export const logout = (): ILogoutAction => ({
  type: AuthActionsTypes.LOGOUT,
});

export const setAuthStatus = (payload: IAuthStatus["payload"]): IAuthStatus => ({
  type: AuthActionsTypes.SET_STATUS,
  payload,
});
export const setAuthToken = (payload: IAuthToken["payload"]): IAuthToken => ({
  type: AuthActionsTypes.SET_TOKEN,
  payload,
});

export type AuthActions = ILoginAction | ILogoutAction | IAuthStatus | IAuthToken;
