import { getDate } from "utils/getDate";
import { ITableHeader, ITableRow } from "components/UI/TableWrapper/types";
import { IShop } from "store/ducks/manage/state";

export const getHeaders = (): ITableHeader[] => [
  { name: "" },
  { name: "id" },
  { name: "" },
  { name: "" },
  { name: "C" },
];

export const getRows = (items: IShop[]): ITableRow[] => {
  return items.map(({ id, name, siteURL, createDate, verifyStatus }: IShop) => ({
    columns: [
      {
        status: verifyStatus,
      },
      {
        content: id,
      },
      {
        content: name,
      },
      {
        content: siteURL,
      },
      {
        content: getDate(createDate),
      },
    ],
  }));
};
