import { Action } from "redux";
import { IShop } from "../manage/state";
import { ICard, IOrder, IPartner } from "./state";

export enum OwnerActionsTypes {
  FETCH_PARTNERS = "owner/FETCH_PARTNERS",
  SET_PARTNERS = "owner/SET_PARTNERS",
  ADD_PARTNER = "owner/ADD_PARTNER",
  EDIT_PARTNER = "owner/EDIT_PARTNER",
  DELETE_PARTNER = "owner/DELETE_PARTNER",

  FETCH_CARDS = "owner/FETCH_CARDS",
  // FETCH_CARD = "owner/FETCH_CARD",
  SET_CARDS = "owner/SET_CARDS",
  ADD_CARD = "owner/ADD_CARD",
  DELETE_CARD = "owner/DELETE_CARD",

  FETCH_ORDERS = "owner/FETCH_ORDERS",
  SET_ORDERS = "owner/SET_ORDERS",

  VERIFY_STATUS = "owner/VERIFY_STATUS",
}

// ACTIONS TYPES
export interface IFetchPartners extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.FETCH_PARTNERS;
}
export interface ISetPartners extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.SET_PARTNERS;
  payload: IPartner[];
}
export interface IAddPartner extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.ADD_PARTNER;
  payload: {
    name: IPartner["name"];
    email: IPartner["email"];
    password: IPartner["password"];
  };
}
export interface IEditPartner extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.EDIT_PARTNER;
  payload: {
    id: IPartner["id"];
    email?: IPartner["email"];
    password?: IPartner["password"];
    description?: IPartner["description"];
    commission: IPartner["commission"];
  };
}
export interface IDeletePartner extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.DELETE_PARTNER;
  payload: IPartner["id"];
}

export interface IFetchCards extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.FETCH_CARDS;
}
// export interface IFetchCard extends Action<OwnerActionsTypes> {
//   type: OwnerActionsTypes.FETCH_CARD;
//   payload: ICard["id"];
// }
export interface ISetCards extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.SET_CARDS;
  payload: ICard[];
}
export interface IAddCard extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.ADD_CARD;
  payload: { cardNumber: string; mm: string; yy: string; cvv: string };
}
export interface IDeleteCard extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.DELETE_CARD;
  payload: ICard["id"];
}

export interface IFetchOrders extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.FETCH_ORDERS;
  payload: { page: number };
}
export interface ISetOrders extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.SET_ORDERS;
  payload: IOrder[];
}

export interface ISetVerifyStatus extends Action<OwnerActionsTypes> {
  type: OwnerActionsTypes.VERIFY_STATUS;
  payload: { id: IShop["id"]; newStatus: IShop["verifyStatus"] };
}

// ACTIONS
export const fetchPartners = (): IFetchPartners => ({
  type: OwnerActionsTypes.FETCH_PARTNERS,
});
export const setPartners = (payload: IPartner[]): ISetPartners => ({
  type: OwnerActionsTypes.SET_PARTNERS,
  payload,
});
export const addPartner = (payload: IAddPartner["payload"]): IAddPartner => ({
  type: OwnerActionsTypes.ADD_PARTNER,
  payload,
});
export const editPartner = (payload: IEditPartner["payload"]): IEditPartner => ({
  type: OwnerActionsTypes.EDIT_PARTNER,
  payload,
});
export const deletePartner = (payload: IDeletePartner["payload"]): IDeletePartner => ({
  type: OwnerActionsTypes.DELETE_PARTNER,
  payload,
});

export const fetchCards = (): IFetchCards => ({
  type: OwnerActionsTypes.FETCH_CARDS,
});
// export const fetchCard = (payload: IFetchCard["payload"]): IFetchCard => ({
//   type: OwnerActionsTypes.FETCH_CARD,
//   payload,
// });
export const setCards = (payload: ICard[]): ISetCards => ({
  type: OwnerActionsTypes.SET_CARDS,
  payload,
});
export const addCard = (payload: IAddCard["payload"]): IAddCard => ({
  type: OwnerActionsTypes.ADD_CARD,
  payload,
});
export const deleteCard = (payload: IDeleteCard["payload"]): IDeleteCard => ({
  type: OwnerActionsTypes.DELETE_CARD,
  payload,
});

export const fetchOrders = (payload: IFetchOrders["payload"]): IFetchOrders => ({
  type: OwnerActionsTypes.FETCH_ORDERS,
  payload,
});
export const setOrders = (payload: IOrder[]): ISetOrders => ({
  type: OwnerActionsTypes.SET_ORDERS,
  payload,
});

export const setVerifyStatus = (payload: ISetVerifyStatus["payload"]): ISetVerifyStatus => ({
  type: OwnerActionsTypes.VERIFY_STATUS,
  payload,
});

export type OwnerActions =
  | IFetchPartners
  | ISetPartners
  | IAddPartner
  | IEditPartner
  | IDeletePartner
  | IFetchCards
  // | IFetchCard
  | ISetCards
  | IAddCard
  | IDeleteCard
  | IFetchOrders
  | ISetOrders
  | ISetVerifyStatus;
