import { IShop, IUser } from "../manage/state";

export enum Status {
  LOADED = "LOADED",
  LOADING = "LOADING",
  ERROR = "ERROR",
  NEVER = "NEVER",
}

export interface IPartner {
  id: number;
  name: string;
  email: string;
  password: string;
  type: number;
  balance: number;
  createDate: number;
  ip: string;
  description: string;
  commission: number;
  removed: boolean;
}

export interface ICard {
  id: number;
  monthTransactions: number;
  dayTransactions: number;
  cardNumber: string;
  mm: string;
  yy: string;
  daySum: number;
  monthSum: number;
}

interface IShopWIthUser extends IShop {
  user: IUser;
}

export interface IOrder {
  id: number;
  v4Id: string;
  status: 1 | 2 | 3;
  amount: number;
  createDate: number;
  payerEmail: string;
  requiredEmail: boolean;
  payerCardNumber: string | null;
  payerIP: string;
  payerCardHolderName: string | null;
  targetCardNumber: string;
  payload: string;
  design: 1 | 2 | 3;
  desc: string;
  shop: IShopWIthUser;
}

export interface ITerminal {
  id: number;
  status: 1 | 2 | 3;
}

export interface IOwner {
  status: Status;
  partners: IPartner[];
  cards: ICard[];
  orders: IOrder[];
  terminals: ITerminal[];
}
