import { FC, ReactElement, useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { Paper } from "@material-ui/core";
import Table from "components/UI/TableWrapper";
// import { EditDialog } from "./EditDialog";

import { getHeaders, getRows } from "./utilsTable";
import { selectOrders } from "store/ducks/owner/selectors";
import { fetchOrders } from "store/ducks/owner/actionCreators";

export const OrdersTable: FC = (): ReactElement => {
  const dispatch = useDispatch();
  const orders = useSelector(selectOrders);

  const headers = getHeaders();
  const rows = getRows(orders);

  const [page, setPage] = useState(0);
  // const [editID, setEditID] = useState<number | null>(null);

  // const onRowClick = (e: any, id: number) => setEditID(id);
  // const handleEditClose = () => setEditID(null);

  useEffect(() => {
    dispatch(fetchOrders({ page }));
  }, [dispatch, page]);

  return (
    <Paper>
      <Table
        headers={headers}
        rows={rows}
        title=" "
        currentPage={page}
        setCurrentPage={setPage} /* onRowClick={onRowClick} */
      />

      {/* <EditDialog id={editID} handleClose={handleEditClose} /> */}
    </Paper>
  );
};
