import { FC, ChangeEvent, useState } from "react";
// import { useDispatch } from "react-redux";
import { useForm } from "react-hook-form";
// @ts-ignore
import { validate } from "wallet-address-validator";

import { TextField, MenuItem } from "@material-ui/core";

import Dialog from "components/UI/Dialog";
import Input from "components/UI/Fields/Input";

// import { addPartner } from "store/ducks/owner/actionCreators";

interface Props {
  open: boolean;
  handleClose: () => void;
  balance: number;
}

interface IDefaultValues {
  wallet: string;
  coin: string;
}

const currencies = [
  {
    value: "BTC",
    label: "",
  },
  {
    value: "USDT",
    label: "",
  },
  {
    value: "ETH",
    label: "",
  },
];

export const NewDialog: FC<Props> = ({ open, handleClose, balance }) => {
  // const dispatch = useDispatch();
  const [currency, setCurrency] = useState("BTC");

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => setCurrency(e.target.value);

  const defaultValues: IDefaultValues = {
    wallet: "",
    coin: "",
  };
  const { control, handleSubmit, watch } = useForm<IDefaultValues>({
    defaultValues,
  });

  const [errors, setErrors] = useState<any>({});
  const wallet = watch("wallet");

  const onSubmit = () => {
    setErrors({});

    if (currency === "USDT") {
      if (!wallet.length) {
        setErrors((prev: any) => ({
          ...prev,
          wallet: "  ",
        }));
        return;
      }
    } else {
      if (!validate(wallet, currency)) {
        setErrors((prev: any) => ({
          ...prev,
          wallet: "  ",
        }));
        return;
      }
    }

    // dispatch(addPartner({ email, name, password }));
    setErrors({});
    handleClose();
  };

  return (
    <Dialog open={open} handleClose={handleClose} title=" " form="newWallet">
      <form id="newWallet" onSubmit={handleSubmit(onSubmit)}>
        <Input
          control={control}
          name="wallet"
          label=""
          type="number"
          maxLength={16}
          defaultValue={defaultValues.wallet}
          error={!!errors.wallet}
          helperText={errors?.wallet}
          required
        />
        <TextField
          label=""
          value={currency}
          onChange={handleChange}
          variant="outlined"
          style={{ marginTop: 16 }}
          select
          required
          fullWidth>
          {currencies.map(option => (
            <MenuItem key={option.value} value={option.value}>
              {option.label} ({option.value})
            </MenuItem>
          ))}
        </TextField>
      </form>
    </Dialog>
  );
};
