import { FC, useState } from "react";
import { useSelector } from "react-redux";

import { Grid, Link } from "@material-ui/core";
import Card from "components/UI/Card";
import { PayoutDialog } from "./PayoutDialog";
import { PaymentsTable } from "./PaymentsTable";
import { WalletsTable } from "./WalletsTable";

import { getPrice } from "utils/getPrice";
import { selectUser } from "store/ducks/manage/selectors";

const Payments: FC = () => {
  const user = useSelector(selectUser);

  const [payoutOpen, setPayoutOpen] = useState(false);
  const handlePayoutClose = () => setPayoutOpen(false);
  const handlePayoutOpen = () => setPayoutOpen(true);

  return (
    <Grid container spacing={3}>
      {user.type === 3 && (
        <>
          <Grid item xs={12} md={6} lg={4}>
            <Card
              title=""
              text={getPrice(user.balance)}
              action={
                <Link href="#" onClick={handlePayoutOpen}>
                  
                </Link>
              }
            />
          </Grid>
          <Grid item xs={12} md={6} lg={4}>
            <Card title="  " text={getPrice(4300)} />
          </Grid>
          <Grid item xs={12} md={6} lg={4}>
            <Card title="  " text={getPrice(1700)} />
          </Grid>
        </>
      )}

      {user.type === 2 || user.type === 3 ? (
        <>
          <Grid item xs={12}>
            <PaymentsTable type="regular" />
          </Grid>
          <Grid item xs={12}>
            <PaymentsTable type="current" />
          </Grid>
        </>
      ) : (
        <>
          <Grid item xs={12}>
            <WalletsTable />
          </Grid>
          <Grid item xs={12}>
            <PaymentsTable type="requests" />
          </Grid>
        </>
      )}

      <PayoutDialog open={payoutOpen} handleClose={handlePayoutClose} balance={user.balance} />
    </Grid>
  );
};

export default Payments;
